
import EpubCFI from "./epubcfi";
import * as rangeUtil from "./utils/range-util";

class SelectionObserver {
    constructor(contents) {
        this.contents = contents;
        this.document = contents.document;
        let eventHandler = (e) => {
            //  touchend, mouseup     .
            if (typeof (iosChecker) == "undefined" || iosChecker == true) { //ios   PC VIEWER
                if (e.type == "selectionchange") {
                    let selection = this.document.getSelection();

                    if (selection) {
                        if (!(selection.type == "Range")) {
                            // this.contents.emit("customSelectCancel");
                            return;
                        }
                        let cfirange = new EpubCFI(selection.getRangeAt(0), this.contents.cfiBase).toString();
                        this.range = cfirange;
                        this.selection = selection;

                        // const selectionRange = selection.getRangeAt(0);
                        // selection.removeAllRanges();
                        // selection.addRange(selectionRange);
                    }
                    else {
                        // this.contents.emit("customSelectCancel");
                    }

                }
                else if (e.type == "mouseup" || e.type == "touchend" || e.type == "touchcancel") {
                    if (typeof (iosChecker) == "undefined" || iosChecker == true || iosChecker == false) {
                        setTimeout(() => {
                            if (!this.selection) return;
                            let { startTop, startLeft, endTop, endLeft } = this.calculateTarget(rangeUtil.selectionFocusRect(this.selection));
                            if (this.range) {
                                this.contents.emit("customSelect", this.range, this.contents, startTop, startLeft, endTop, endLeft, this.selection.toString(), this.contents);
                            }
                        }, 10)
                    }

                }
                else if (e.type == "mousedown" || e.type == "touchstart") {
                    // if (this.selection) {
                    //     this.selection.removeAllRanges();
                    // }
                    this.selection = null;
                    this.contents.emit("customSelectCancel");
                }
                else if (e.type == "touchmove") {
                    e.preventDefault();
                }
            }
            else { // 
                if (e.type == "selectionchange") {
                    let selection = this.document.getSelection();
                    this.selection = selection;
                    if (selection) {
                        if (!(selection.type == "Range")) {
                            return;
                        }
                        setTimeout(() => {
                            if (!this.selection) return;
                            let { startTop, startLeft, endTop, endLeft } = this.calculateTarget(rangeUtil.selectionFocusRect(this.selection));
                            let cfirange = new EpubCFI(selection.getRangeAt(0), this.contents.cfiBase).toString();
                            this.range = cfirange;
                            if (this.range) {
                                this.contents.emit("customSelect", this.range, this.contents, startTop, startLeft, endTop, endLeft, this.selection.toString(), this.contents);
                            }
                        }, 10)
                    }
                }

                else if (e.type == "touchstart" || e.type == "mousedown") {
                    this.selection = null;
                    this.contents.emit("customSelectCancel");
                }
                else if (e.type == "touchend" || e.type == "mouseup") {

                }
            }
        }
        this.eventHandler = eventHandler;
    }

    addListener() {
        this.document.addEventListener("mouseup", this.eventHandler, true);
        this.document.addEventListener("mousedown", this.eventHandler);
        this.document.addEventListener("touchstart", this.eventHandler);
        this.document.addEventListener("touchend", this.eventHandler, true);
        this.document.addEventListener("touchcancel", this.eventHandler);
        this.document.addEventListener("touchmove", this.eventHandler);
        this.document.addEventListener("selectionchange", this.eventHandler);
    }
    removeListener() {
        this.document.removeEventListener("mouseup", this.eventHandler);
        this.document.removeEventListener("mousedown", this.eventHandler);
        this.document.removeEventListener("touchstart", this.eventHandler);
        this.document.removeEventListener("touchend", this.eventHandler);
        this.document.removeEventListener("touchcancel", this.eventHandler);
        this.document.removeEventListener("touchmove", this.eventHandler);
        this.document.removeEventListener("selectionchange", this.eventHandler);
    }

    getLoc(range) {
        let textBoxes = rangeUtil.getTextBoundingBoxes(range);
        let loc = {};
        loc.start = textBoxes[0];
        loc.end = textBoxes[textBoxes.length - 1];
        return this.calculateTarget(loc);
    }

    calculateTarget(selectionRect) {
        if (selectionRect) {
            let startTop;
            let startLeft;
            let endTop;
            let endLeft;
            startLeft = selectionRect.start.left;
            endLeft = selectionRect.end.left + selectionRect.end.width;
            startTop = selectionRect.start.top;
            endTop = selectionRect.end.top + selectionRect.end.height;


            // Constrain the adder to the viewport.
            // startLeft = Math.max(startLeft, 0);
            // endLeft = Math.min(endLeft, this.document.innerWidth);
            // startTop = Math.max(startTop, 0);
            // endTop = Math.min(endTop, this.document.innerHeight);
            // let views = rendition.views()._views;
            // for(let i=0;i<views.length;i++){
            //     if(!(views[i].contents == this.contents)){
            //         top += views[i].contents.clientHeight;
            //     }
            // }


            return { startTop, startLeft, endTop, endLeft };
        }
        else {
            return { top: -9999, left: -9999 };
        }

    }
}


export default SelectionObserver;